<template>	
	<div class="top">
		<div class="msg">
			<div class="fl logo"><router-link :to="{path:'/'}">大数据驱动的智慧审计</router-link></div>
			<div class="fr search-box">
				<Search/>
			</div>
		</div>
		<div class="date">
			<div class="companyBox fl" style="color:#b80000;"><i class="am-icon-home" style="zoom:1.2"></i> 当前所审公司为：<span style="color:#4d4d4d;">{{companyName?companyName.value:''}}</span></div>
			<div class="wordBox fl" v-if="getWord" style="color:#b80000;margin-left: 20px"><i class="am-icon-exclamation-triangle" style="zoom:1.1"></i> 异常指标：<span style="color:#4d4d4d;">{{getWord1[0]}}<span v-if="getWord1[1]">===>{{getWord1[1]}}</span></span></div>
			<!-- <div class="welcome-info fr">
				<div>尊敬的用户<span id="weather"></span></div>
			</div> -->
			
		</div>
	</div>
</template>
<script type="text/javascript">
	import './../assets/js/jquery.leoweather.min.js'
	import Search from './search'
	// import Login from './login'
	export default{
		name:'top',
		data(){
			return{
				dialogVisible:false
			}
		},
		created:function() {

		},		 
		mounted() {
			console.log(this.$router);
			$('#weather').leoweather({format:'，{时段}好！<span id="colock">现在时间是：{年}年{月}月{日}日 星期{周} {时}:{分}:{秒}</span>'});
		},
		components:{
			Search,
			// Login
		},
		computed:{
			companyName(){
				return JSON.parse(this.$store.getters.getCompany)
			},
			getWord(){
				return this.$store.getters.getWord;
			},
			getWord1(){
				return this.$store.getters.getWord1;
			},
			getRouter(){
				return this.router;
			}
		}
	}
</script>
<style type="text/css" scoped="">
	.top{
		width: 100%;
	}
	.msg{
		position: fixed;
		top: 0;
		width: 100%;
		height: 100px;
		z-index: 999;
    	line-height: 100px;
    	background: linear-gradient(#990202, #b20202);
	}
	.logo a{
		display: inline-block;
		color: #fff;
		font-size: 40px;
		margin-left: 40px;
	}
	.date{
		position: fixed;
	    width: 100%;
	    top: 100px;
	    height: 50px;
	    line-height: 50px;
	    z-index: 99;
		padding-left: 370px;
    	box-sizing: border-box;
		background-color: #e6eaf0;
		border-bottom: 1px solid #d5d9de;
		white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.date .companyBox{
		margin-left: 580px;
	}
	.date .welcome-info{
		color: #4d4d4d;
		margin-right: 80px;
		font-size: 12px
	}
	.top .search-box{
		width: 450px;
		margin-right: 360px;
	}
	.top .user{
		margin-left: 10px;
		margin-right: 100px;
		margin-top: 6px;
	}
	.top .login-box{
		margin-left: 10px;
		margin-right:70px;
		padding-top: 14px;
	}
	.top .login-box{
		margin-left: 30px;
		margin-top: -10px;
	}
	.top .search{
		position: relative;
	    width: 100%;
	    margin-top: 30px;
	    border-radius: 250px;
	    background-color: #d90000;
	    height: 40px;
	    line-height: 40px;
		background-size: 100%;
	}
	@media only screen and (max-width: 1400px){
		.top .search-box{
		    margin-right: 300px;
		}
	}
	@media only screen and (max-width: 1280px){
		.top .search-box{
		    margin-right: 250px;
		}
	}
</style>


