<template>
	<div class="left">
		<ul class="nav-list">
			<li v-for='(nav,index) in nav_list'>
				<div class="title" :class="{active:activeList===index}" @click="activeList=index"><i class="icon" :class="nav.icon_class"></i>{{nav.title}}<i class="fr icon-caret" :class="(activeList===index)?'am-icon-caret-down':'am-icon-caret-right'"></i></div>
				<ul class="navbar-list" v-show="activeList===index">
					<li v-for="(navbar,index2) in nav.navbar_list">
						<div class="line"></div>
						<router-link name="place" :class="{disabled:index==1&&index2>0&&(!getWord)}" active-class="active" class="item" :to="{path:navbar.page}">{{navbar.name}}</router-link>
						<!-- <a class="item">{{navbar}}</a> -->
					</li>
				</ul>
				<div class="line"></div>
			</li>
		</ul>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:'left',
		data(){
			return{
				activeList:0,
				nav_list:[
					{
						title:'审计准备',
						icon_class:'icon1',
						navbar_list:[
							{
								name:'企业画像',
								page:'/inside/page1_5'
							},
							{
								name:'审计法规及依据',
								page:'/inside/page1_2',
								list:['法律法规','行业规范','业务逻辑','勾稽关系']
							},
							{
								name:'审计方向及重点',
								page:'/inside/page1_1',
								list:['中央政策与地方政策','地方政策与工作纪要','中央政策与工作纪要']
							},
							{
								name:'审计案例及经验',
								page:'/inside/page1_4',
								// list:['法律法规','行业规范','业务逻辑','勾稽关系']
							},
							{
								name:'审计数据录入',
								page:'/inside/page1_3',
								list:['财务指标','业务指标']
							}
						]
					},
					{
						title:'审计疑点线索挖掘',
						icon_class:'icon2',
						navbar_list:[
							{
								name:'发现疑点',
								page:'/inside/page2_4',
							},
							{
								name:'合规审计',
								page:'/inside/page2_1',
								list:['与政策法规的比较','与行业规范的比较','与业务逻辑的比较','与勾稽关系的比较']
							},
							{
								name:'真实审计',
								page:'/inside/page2_2',
								list:['交叉验证','协同验证','证据链追溯']
							},
							{
								name:'绩效审计',
								page:'/inside/page2_3',
								list:['与计划目标比较','与同行业平均水平比较']
							},
							{
								name:'综合分析',
								page:'/inside/page2_5'
							}
						]
					},
					{
						title:'审计证据存储及追溯',
						icon_class:'icon3',
						navbar_list:[
							{
								name:'证据生成',
								page:'/inside/page3_1'
							},
							{
								name:'证据展示',
								page:'/inside/page3_2'
							},
							{
								name:'证据追溯',
								page:'/inside/page3_3'
							},
							{
								name:'证据库',
								page:'/inside/page3_4'
							},
							// {
							// 	name:'底稿追溯',
							// 	page:'/inside/page3_5'
							// }
						]
					},
					{
						title:'审计报告自动生成',
						icon_class:'icon4',
						navbar_list:[
							{
								name:'报告准备',
								page:'/inside/page4_1'
							},
							// {
							// 	name:'报告内容',
							// 	page:'/inside/page4_2'
							// },
							{
								name:'报告纠偏',
								page:'/inside/page4_4'
							},
							{
								name:'初稿生成',
								page:'/inside/page4_3'
							}
						]
					}
				]
			}
		},
		computed:{
			getWord(){
				return this.$store.getters.getWord;
			}
		},
		methods:{
			
		}
	}
</script>
<style type="text/css" scoped="">
	.left{
		position: fixed;
		top:90px;
		left: 0;
		bottom: 0;
		z-index: 99;
		width: 370px;
    	overflow-y: auto;
		/*padding: 40px 17px 40px 5px;*/
		box-sizing: border-box;
		background-color: #28343f;
	}
	ul{
		padding-left: 0;
	}
	.nav-list{
		margin-bottom: 60px;
	}
	.nav-list>li{
		text-indent: 20px;
	}
	.nav-list .title{
		cursor: pointer;
		color: #fff;
		font-size: 20px;
		height: 80px;
    	line-height: 80px;
	}
	.icon-caret{
		margin-top: 28px;
		margin-right: 10px;
	}
	.nav-list .title .icon{
		display: inline-block;
		vertical-align: middle;
		text-align: center;
		width: 100px;
		height: 100px;
		zoom:0.6;
		background: url('../assets/left_icons.png') no-repeat;
	}
	.nav-list .title .icon1{
		background-position: 74% -30px;
	}
	.nav-list .title .icon2{
		background-position: 74% 50%;
	}
	.nav-list .title .icon3{
		background-position: 94% -30px;
	}
	.nav-list .title .icon4{
		background-position: 74% 88%;
	}
	.nav-list .line{
		width: 100%;
		height: 1px;
		background-color: #1f262d;
    	border-bottom: 1px solid #303f4d;
	}
	.nav-list .navbar-list{
		margin: 0;
		/*margin-bottom: 6px;*/
	}			
	.nav-list .navbar-list li{
		width: 100%;
		text-indent: 80px;
	}
	.nav-list .navbar-list li .item{
		display: block;
		color: #fff;
		font-size: 18px;
		height: 68px;
    	line-height: 68px;
		cursor: pointer;
		width: 100%;
		background-color: #2d3b47;
	}
	.nav-list .navbar-list li .item:hover,.nav-list .navbar-list li .item.active{
		color: #cc0000;
	    border-left: 4px solid #cc0000;
	    font-size: 18px;
	    background-color: transparent;
	}
	.nav-list .navbar-list li .item.disabled{
		display: none;
		cursor: not-allowed;
	}

	
</style>


