<template>
	<div class="inside">
		<Top/>
		<div class="login-box fr">
			<Login biaoshi="zhupingtai"/>
		</div>
		<Left/>
		<div class="right">
			<div class="content" v-if="isRouterAlive">
				<router-view/>
			</div>
		</div>		
	</div>
</template>
<script type="text/javascript">
	import Top from './top'
	import Left from './left'
	import Login from './login'
	import '../../static/jquery.textSearch.js'
	export default{
		name:'inside',
		provide () {
		    return {
		      reload: this.reload
		   }
		},
		data(){
			return{
				isRouterAlive:true
			}
		},
		components:{
			Top,
			Left,
			Login
		},
		methods:{
			reload () {
		      this.isRouterAlive = false
		      this.$nextTick(() => (this.isRouterAlive = true))
		    }
		},
		mounted() {
			// $(".content").textSearch("招商银行 招商 招商银行股份有限公司",{markColor: "blue"});
		},
		computed:{
			
		}
	}
</script>
<style type="text/css" scoped="">
	.inside{
		position: relative;
		width: 100%;
		height: 100%;
	}
	.inside>.right{
		width: 100%;
		padding-top: 180px;
		padding-left: 390px;
		padding-right: 10px;
		box-sizing: border-box;
	}
	
	
</style>


